.card {
    width: 180px;
    margin: 16px 20px;
    float: left;
}

.card-text {
    text-align: center;
}

.card-img-top {
    height: 190px;
}

.card img {
    width: 100%;
    position: relative;
    top: 50%;
    -webkit-transform: perspective(1px) translateY(-50%);
            transform: perspective(1px) translateY(-50%);
    max-height: 190px;
}

.card-container {
    margin: 0 auto;
    width: 910px;
}
.jumbotron {
    margin: 55px 0 10px;
    text-align: center;
    color: black;
    background: none;
}

.jumbotron .display-4 {
    font-size: 2.7rem;
}

.lead {
    width: 80%;
    margin: 0 auto;
}
.navbar h5 {
    margin-bottom: 0 !important;
}

.navbar {
    background-color: #013243 !important;
    color: white;
    display: inline-block;
}

.navbar a {
    color: white !important;
    font-weight: bolder;
    letter-spacing: 1px;
}

.navbar-score {
    float: right !important;
    text-align: right !important;
    padding-top: .4rem;
}
.modal {
    margin-top: 100px;
    overflow: visible !important;
}

.modal-title {
    float: left;
}
